.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(0, 0, 0);

  overflow: hidden;
  overflow-y: auto;
  transition: opacity 0.3s;
  z-index: 100;
}

.modalContent {
  position: relative;
  top: 50%;
  left: 50%;
  padding: 20px;
  display: flex;
  flex-direction: column-reverse;
  align-items: center;

  width: 100%;
  max-width: 600px;

  border-radius: 16px;
  background-color: #ffffff;
  padding: 40px 20px 20px 20px;
  transform: translate(-50%, -50%);
  transition: opacity 0.3s, transform 0.3s;
  z-index: 150;
}
.modalCloseButton {
  position: absolute;
  top: 16px;
  right: 16px;
  width: 30px;
  height: 30px;
  padding: 0;
  background-color: transparent;
  border-color: transparent;
}

.closeIcon {
  width: 30px;
  height: 30px;
  transition: 500ms;
  &:hover,
  &:focus {
    scale: 1.4;
  }
}

.headerTitleModal {
  position: absolute;
  top: 16px;
  left: 16px;
  color: #000;
  font-family: Crimson Text;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: 140.1%;

  @media (min-width: 1020px) {
    font-size: 40px;
  }
}

.iconCloseBlog {
  display: block;
  width: 120px;
  height: 120px;
  position: absolute;
  top: -48px;
  right: -48px;
  background-color: #000;
  border-radius: 50%;
  z-index: -1;

  @media screen and (min-width: 769px) {
    width: 158px;
    height: 158px;
    top: -68px;
    right: -68px;
  }
}
